@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}


p,
h1 {
  font-family: 'Roboto', sans-serif;
}

body {
  font-family: 'Jost', sans-serif;
}

span {
  font-family: 'Jost', sans-serif;
}


h2 {
  font-family: 'Jost', sans-serif;
}

h3 {
  font-family: 'Jost', sans-serif;
}


button {
  font-family: 'Jost', sans-serif;
}